                            <li id="tab2" class="btnCon"><input type="radio" name="tabmenu" id="tabmenu2">
                                <label for="tabmenu2">개인정보 처리방침</label>
                                <div class="tabCon" >
                                    개인정보처리방침<br>
                                    <br>
                                    개인정보 수집 및 이용 안내<br>
                                    주식회사 odo(이하 “회사”)는 회원님의 개인정보를 보호하기 위해 최선을 다하고 있습니다. 이를 위해서 회사는 개인정보의 보호와 관련하여 ‘정보통신망 이용촉진 및 정보보호 등에 관한 법률’, ‘개인정보 보호법’ 등 개인정보와 관련된 법령을 준수하고 있습니다.<br>
                                    <br>
                                    1. 수집하는 개인정보의 항목<br>
                                    회사는 이름, 이메일, 나이, 성별, 서비스 이용기록, 결제 및 환불 기록, 생년월일, 휴대폰번호, 관심분야 등에 관한 정보를 수집합니다.<br>
                                    서비스를 이용하는 과정에서 방문일시, 사용이력, 기기정보, 접속로그, IP주소 등이 자동으로 생성·수집 될 수 있습니다.<br>
                                    회사는 회원가입, 구매 등 서비스 제공을 위한 최소한의 범위 내에서 이용자의 동의 하에 개인정보를 수집하며, 수집한 모든 개인정보는 고지한 목적 범위 내에서만 이용됩니다.<br>
                                    1) 회원 가입 시<br>
                                    - (필수) 아이디(E-MAIL), 비밀번호, 휴대폰번호, 닉네임<br>
                                    - (선택) 생년월일, 성별, 연계정보(CI)<br>
                                    2) 판매 회원 가입 시<br>
                                    - (필수) 호스트명, 이름, 아이디, 비밀번호, E-MAIL, 사업자등록번호, 주민등록증, 담당자 이름, 휴대폰번호, 주소, 호스트 약력, 은행명, 예금주, 계좌번호<br>
                                    - (선택) 호스트등록경로, 프로필 사진<br>
                                    3) 상품 구매 시<br>
                                    - (필수) 핸드폰번호, 신용카드 정보, 은행계좌 정보, 결제기록 등의 정보<br>
                                    - (선택) 배송지 정보: 수령인, 연락처, 주소지<br>
                                    - (선택) 탑승자 이름, 동반인정보(성명, 이외 서비스의 유형에 따라, 추가적인 정보가 수집될 수 있습니다. (여행상품)<br>
                                    4) 모바일 사용 시<br>
                                    - 프립 앱 버전, OS버전 (iOS, 안드로이드)<br>
                                    - 위치정보 (별도 저장없이 앱에서 사용)<br>
                                    5) 기타<br>
                                    - 서비스 이용과정에서 자동 수집 정보 : 회원ID, IP Address, 쿠키, 방문 일시, 서비스 이용 기록, 불량 이용 기록, 기기정보, ADID, IDFA, 위치정보<br>
                                    -고객 분쟁처리 및 상담 진행 시 : 상담내역, 채팅(대화)내역, 서비스 이용기록, 결제 및 환불 기록<br>
                                    6) 구독회원<br>
                                    - E-MAIL, 성별<br>
                                    <br>
                                    2. 개인정보의 수집 및 이용목적<br>
